/* */
$red: #ff0000;

div.container {
  h3 {
    color: $red;
  }
  p {
    color: $red;
  }
}

@mixin 함수() {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}
.my-alert {
  @include 함수();
}
.my-alert p {
  margin-bottom: 0;
}

.my-alert2 {
  @extend .my-alert;
  background: yellow;
}
